<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-table :data="rightsList" border stripe height="480">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="authName"></el-table-column>
        <el-table-column label="" prop="path"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tag size="small" v-if="scope.row.level == 0"></el-tag>
            <el-tag type="success" size="small" v-else-if="scope.row.level == 1"></el-tag>
            <el-tag type="warning" size="small" v-else></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
import mix from './Rights-mixins.js'
export default {
  mixins: [mix]
}
</script>

<style lang="less" scoped>
</style>



