// vs code    ctrl + k + 0
export default {
  data() {
    return {
      // 
      rolesList: [],
      // 
      setRightDialogVisible: false,
      // 
      rightTree: [],
      // 
      treeConfig: {
        children: 'children', // 
        label: 'authName' // 
      },
      // id
      defaultCheckedKeys: [],
      // Id
      selectedRoleId: null
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    // 
    async getRolesList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error('')
      this.rolesList = res.data
      console.log(res.data)
    },
    // IdId
    async removeRight(role, rightId) {
      // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      // 
      if (confirmResult !== 'confirm') {
        return this.$message({
          type: 'info',
          message: ''
        })
      }

      // 
      const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      //  
      // this.getRolesList()

      // res.data // 
      //   children  res.data 
      role.children = res.data
    },
    // 
    async showSetRightDialog(role) {
      // Iddata
      this.selectedRoleId = role.id
      //  
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) return this.$message.error('')
      // data
      this.rightTree = res.data
      // Id
      const keys = [] // Id
      this.getLeafIds(role, keys)
      this.defaultCheckedKeys = keys
      this.setRightDialogVisible = true
    },
    // keysId
    getLeafIds(node, keys) {
      if (!node.children) {
        keys.push(node.id)
      } else {
        node.children.forEach(item => this.getLeafIds(item, keys))
      }
    },
    // 
    resetSetRightDialog() {
      this.rightTree = []
      this.defaultCheckedKeys = []
      this.selectedRoleId = null
    },
    // 
    async saveRight() {
      // 1. Id
      const arr1 = this.$refs.tree.getCheckedKeys()
      const arr2 = this.$refs.tree.getHalfCheckedKeys()
      const rids = [...arr1, ...arr2].join(',')
      // 2. 
      // 3.  .join(',')  rids 
      // 4.  API 
      const { data: res } = await this.$http.post(`roles/${this.selectedRoleId}/rights`, { rids })
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.setRightDialogVisible = false
      this.getRolesList()
    }
  }
}



