export default {
  data() {
    return {
      // 
      cateList: [],
      // 
      cascaderConfig: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      selectedCateList: [],
      // 
      activeName: 'many',
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      addDialogVisible: false,
      // 
      addForm: {
        attr_name: ''
      },
      //  
      addFormRules: {
        attr_name: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created() {
    this.getAllCateList()
  },
  methods: {
    // 
    async getAllCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) return this.$message.error('')
      this.cateList = res.data
    },
    // 
    cascaderChanged() {
      if (this.selectedCateList.length !== 3) {
        // 
        this.selectedCateList = []
        this.manyTableData = []
        this.onlyTableData = []
      } else {
        // 
        // 
        //  getParamsList 
        this.getParamsList()
      }

      console.log(this.selectedCateList)
    },
    //  Id  
    async getParamsList() {
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
        params: { sel: this.activeName }
      })
      if (res.meta.status !== 200) return this.$message.error('')
      //  attr_vals 
      res.data.forEach(item => {
        //  attr_vals
        // item.attr_vals split Tag
        item.attr_vals = item.attr_vals.length > 0 ? item.attr_vals.split(' ') : []
        // TagInput
        //  tagInputVisible  
        item.tagInputVisible = false
        //  item.tagInputValue 
        item.tagInputValue = ''
      })
      // data
      if (this.activeName === 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
      console.log(res.data)
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addNewParams() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return

        const { data: res } = await this.$http.post(`categories/${this.cateId}/attributes`, {
          // 
          attr_name: this.addForm.attr_name,
          //    many  only
          attr_sel: this.activeName
        })

        if (res.meta.status !== 201) return this.$message.error('')
        this.$message.success('')
        // 
        this.getParamsList()
        // 
        this.addDialogVisible = false
      })
    },
    // TagInput
    showTagInput(row) {
      row.tagInputVisible = true
      //  data  tagInputVisible 
      //   $nextTick 
      // $nextTick  DOM 
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // 
    async loseBlur(row) {
      row.tagInputVisible = false
      // 
      if (row.tagInputValue.trim().length <= 0) {
        row.tagInputValue = ''
        return
      }
      console.log('')
      // push
      row.attr_vals.push(row.tagInputValue)
      row.tagInputValue = ''
      const { data: res } = await this.$http.put(
        `categories/${this.cateId}/attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_sel: row.attr_sel,
          attr_vals: row.attr_vals.join(' ')
        }
      )

      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
    },
    // 
    async removeTag(row, index) {
      // 1.  attr_vals 
      row.attr_vals.splice(index, 1)
      // 2. 
      const { data: res } = await this.$http.put(
        `categories/${this.cateId}/attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_sel: row.attr_sel,
          attr_vals: row.attr_vals.join(' ')
        }
      )

      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
    }
  },
  computed: {
    // Id
    //  null  Id
    //  cateId  null 
    cateId() {
      if (this.selectedCateList.length === 3) {
        return this.selectedCateList[this.selectedCateList.length - 1]
      } else {
        return null
      }
    },
    //   
    isDisable() {
      if (this.selectedCateList.length === 3) {
        return false
      } else {
        return true
      }
      // return this.selectedCateList.length === 3 ? false : true
    }
  },
  watch: {
    activeName(newVal, oldVal) {
      //   cateId nullId
      // 
      if (this.cateId !== null) {
        this.getParamsList()
      }
    }
  }
}

