<template>
  <el-container class="home-container">
    <!--  -->
    <el-header>
      <div class="left">
        <img src="../../assets/zanduoduo.png" alt="">
        <span></span>
      </div>
      <el-button type="info" @click="logout"></el-button>
    </el-header>
    <el-container>
      <!--  -->
      <el-aside :width="collapse ? '65px' : '200px'">
        <!--  -->
        <div class="toggleBar" @click="collapse=!collapse">|||</div>
        <!-- Start:  -->
        <el-menu background-color="#333744" text-color="#fff" active-text-color="#409EFF" unique-opened :collapse="collapse" :collapse-transition="false" router :default-active="activePath">
          <!-- el-submenu  -->
          <el-submenu :index="item.id + ''" v-for="(item, i) in menus" :key="item.id" :style="collapse ? 'width: 65px;' : 'width: 200px;'">
            <!--  template  -->
            <template slot="title">
              <i :class="['iconfont', iconlist[i]]"></i>
              <span>{{item.authName}}</span>
            </template>
            <!--  -->
            <el-menu-item :index="'/' + subItem.path" v-for="subItem in item.children" :key="subItem.id" @click="saveActivePath('/' + subItem.path)">
              <!--  -->
              <i class="el-icon-menu"></i>
              <!--  -->
              <span slot="title">{{subItem.authName}}</span>
            </el-menu-item>
          </el-submenu>
        </el-menu>
        <!-- Ended -->
      </el-aside>
      <!--  -->
      <el-main>
        <!--  -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import mix from './Home-mixins.js'
export default {
  mixins: [mix]
}
</script>

<style lang="less" scoped>
// 
.home-container {
  height: 100%;

  // 
  .el-header {
    user-select: none;
    background-color: #373d41;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 0;
    .left {
      color: white;
      font-size: 22px;
      display: flex;
      align-items: center;
      img {
        margin-right: 10px;
        height: 50px;
      }
    }
  }

  // 
  .el-aside {
    background-color: #333744;
    user-select: none;
  }

  // 
  .el-main {
    background-color: #eaedf1;
  }
}

.iconfont {
  margin-right: 8px;
}

.toggleBar {
  line-height: 25px;
  color: white;
  background-color: #4a5064;
  text-align: center;
  font-size: 12px;
  letter-spacing: 0.1em;
  cursor: pointer;
}
</style>



