//  
import cityOptions from './city_data2017_element.js'

export default {
  data() {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      orderList: [],
      // 
      total: 0,
      // 
      wlDialogVisible: false,
      // 
      wlList: [],
      // 
      addressDialogVisible: false,
      // 
      addressForm: {
        area: '',
        address: ''
      },
      addressFormRules: {
        area: [{ required: true, message: '', trigger: 'blur' }],
        address: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      cityOptions,
      // 
      selectedArea: []
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    // 
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', { params: this.queryInfo })
      if (res.meta.status !== 200) return this.$message.error('')
      this.orderList = res.data.goods
      this.total = res.data.total
      console.log(res)
    },
    // pageSize 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    // 
    async showWLDialog() {
      // 
      const { data: res } = await this.$http.get('/kuaidi/1106975712662')
      if (res.meta.status !== 200) return this.$message.error('')
      console.log(res)
      this.wlList = res.data
      this.wlDialogVisible = true
    },
    // 
    changeProvince() {
      console.log(this.selectedArea)
    }
  }
}



