export default {
  data() {
    // 
    const checkEmail = (rule, value, cb) => {
      if (
        /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/.test(value)
      ) {
        // 
        cb()
      } else {
        // 
        cb(new Error(''))
      }
    }

    // 
    const checkMobile = (rule, value, cb) => {
      if (/^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/.test(value)) {
        cb()
      } else {
        cb(new Error(''))
      }
    }

    return {
      // 
      queryInfo: {
        query: '', // 
        pagenum: 1, // 
        pagesize: 5 // 2
      },
      total: 0, // 
      userlist: [], // 
      // 
      addDialogVisible: false,
      // 
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 
      addFormRules: {
        username: [{ required: true, message: '', trigger: 'blur' }],
        password: [{ required: true, message: '', trigger: 'blur' }],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // 
      setRoleDialogVidible: false,
      // 
      userInfo: {},
      // 
      rolesList: [],
      // Id
      selectedRoleId: ''
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    // 
    async getUserList() {
      const { data: res } = await this.$http.get('users', { params: this.queryInfo })
      if (res.meta.status !== 200) return this.$message.error('')
      this.total = res.data.total
      this.userlist = res.data.users
    },
    // 
    async stateChanged(id, newState) {
      const { data: res } = await this.$http.put(`users/${id}/state/${newState}`)
      if (res.meta.status !== 200) return this.$message.error('')
    },
    //  pageSize 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    //   
    handleCurrentChange(newPage) {
      //  
      this.queryInfo.pagenum = newPage
      // 
      this.getUserList()
    },
    // 
    resetAddForm() {
      // 
      this.$refs.addFormRef.resetFields()
      // 
      this.addDialogVisible = false
    },
    // 
    addUser() {
      // 1. 
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('users', this.addForm)
        if (res.meta.status !== 201) return this.$message.error('')
        this.$message.success('')
        this.addDialogVisible = false
        this.getUserList()
      })
    },
    // 
    async showEditDialog(id) {
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) return this.$message.error('')
      //  
      this.editForm = res.data
      this.editDialogVisible = true
    },
    // 
    resetEditForm() {
      // 
      this.editForm = {}
      // 
      this.$refs.editFormRef.resetFields()
      this.editDialogVisible = false
    },
    // 
    saveUserInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        // 
        const { data: res } = await this.$http.put('users/' + this.editForm.id, {
          email: this.editForm.email,
          mobile: this.editForm.mobile
        })

        if (res.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        this.getUserList()
        this.editDialogVisible = false
      })
    },
    // Id
    async remove(id) {
      // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      // 
      if (confirmResult !== 'confirm') {
        return this.$message({
          type: 'info',
          message: ''
        })
      }

      // 
      const { data: res } = await this.$http.delete('users/' + id)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getUserList()
    },
    // 
    async showSetRoleDialog(userInfo) {
      console.log(userInfo)
      this.userInfo = userInfo
      // 
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error('')
      this.rolesList = res.data
      this.setRoleDialogVidible = true
    },
    // 
    resetRoleDialog() {
      // Id
      this.selectedRoleId = ''
      // 
      this.rolesList = []
      this.setRoleDialogVidible = false
    },
    // 
    async saveNewRole() {
      if (this.selectedRoleId === '') return this.$message.error('')

      // 
      const { data: res } = await this.$http.put(`users/${this.userInfo.id}/role`, {
        rid: this.selectedRoleId
      })
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getUserList()
      this.setRoleDialogVidible = false
    }
  }
}



