<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-button type="primary"></el-button>

      <!--  -->
      <el-table :data="rolesList" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <!--  scope.row  -->
            <!--  scope.row.children  -->
            <!--   scope.row.children  -->
            <el-row v-for="(item1, i1) in scope.row.children" :key="item1.id" :style="{'border-bottom': '1px solid #eee', 'border-top': i1 === 0 ? '1px solid #eee' : ''}" class="centerRow">
              <!--   -->
              <el-col :span="5">
                <el-tag closable @close="removeRight(scope.row, item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  19   -->
              <el-col :span="19">
                <!--  row   19  24  -->
                <!--  5  19  -->
                <el-row v-for="(item2, i2) in item1.children" :key="item2.id" :style="{'border-top': i2 === 0 ? '' : '1px solid #eee'}" class="centerRow">
                  <!--  -->
                  <el-col :span="6">
                    <el-tag closable type="success" @close="removeRight(scope.row, item2.id)">{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!--  -->
                  <el-col :span="18">
                    <el-tag closable type="warning" v-for="item3 in item2.children" :key="item3.id" @close="removeRight(scope.row, item3.id)">{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini"></el-button>
            <el-button type="warning" icon="el-icon-setting" size="mini" @click="showSetRightDialog(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="setRightDialogVisible" width="50%" @close="resetSetRightDialog">
      <!-- data  -->
      <!-- props  -->
      <!-- show-checkbox  -->
      <!-- node-key  -->
      <!-- default-expand-all  -->
      <el-tree ref="tree" :data="rightTree" :props="treeConfig" show-checkbox node-key="id" default-expand-all :default-checked-keys="defaultCheckedKeys"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveRight"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import mix from './Roles-mixins.js'
export default {
  mixins: [mix]
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 10px 5px;
}

.centerRow {
  display: flex;
  align-items: center;
}
</style>



