export default {
  data() {
    return {
      // 
      menus: [],
      collapse: false,
      iconlist: ['icon-users', 'icon-tijikongjian', 'icon-shangpin', 'icon-danju', 'icon-baobiao'],
      // path
      activePath: ''
    }
  },
  created() {
    // this.getMenus()
    this.menus = [
      {
        authName: '',
        id: 0,
        children: [
          {
            authName: '',
            path: 'newTask'
          }
        ]
      },
      {
        authName: '',
        id: 1,
        children: [
          {
            authName: '',
            path: 'member'
          }
        ]
      },
      {
        authName: '',
        id: 2,
        children: [
          {
            authName: '',
            path: 'task'
          }
        ]
      },
      {
        authName: '',
        id: 3,
        children: [
          {
            authName: '',
            path: 'withDrawal'
          }
        ]
      }
    ]
    // Homedata
    //  sessionStorage 
    //  data  activePath 
    const ap = window.sessionStorage.getItem('activePath')
    this.activePath = ap
  },
  methods: {
    // 
    logout() {
      window.sessionStorage.clear()
      this.$router.push('/login')
    },
    // 
    async getMenus() {
      const { data: res } = await this.$http.get('menus')
      if (res.meta.status !== 200) return this.$message.error('')
      this.menus = res.data
    },
    //  index 
    saveActivePath(activePath) {
      console.log(activePath)
      this.activePath = activePath
      //  sessionStorage
      window.sessionStorage.setItem('activePath', activePath)
    }
  }
}



