<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title="" type="warning" show-icon :closable="false"></el-alert>

      <!--  -->
      <div class="cascader">
        <span></span>
        <!--  -->
        <el-cascader expand-trigger="hover" :options="cateList" :props="cascaderConfig" v-model="selectedCateList" @change="cascaderChanged">
        </el-cascader>
      </div>

      <!-- Tab  -->
      <el-tabs v-model="activeName">
        <el-tab-pane label="" name="many">
          <el-button type="primary" size="mini" :disabled="isDisable" @click="addDialogVisible=true"></el-button>
          <!--  -->
          <el-table :data="manyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                {{scope.row}}
                <hr/>
                <!--  attr_vals  tag  -->
                <el-tag closable v-for="(item, i) in scope.row.attr_vals" :key="i" @close="removeTag(scope.row, i)">{{item}}</el-tag>
                <!--  tag  -->
                <el-input size="small" class="tagInput" v-if="scope.row.tagInputVisible" ref="saveTagInput" @blur="loseBlur(scope.row)" @keyup.enter.native="loseBlur(scope.row)" v-model="scope.row.tagInputValue"></el-input>
                <!--  -->
                <el-button size="small" v-else @click="showTagInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="only">
          <el-button type="primary" size="mini" :disabled="isDisable" @click="addDialogVisible=true"></el-button>
          <!--  -->
          <el-table :data="onlyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!--  attr_vals  tag  -->
                <el-tag closable v-for="(item, i) in scope.row.attr_vals" :key="i" @close="removeTag(scope.row, i)">{{item}}</el-tag>
                <!--  tag  -->
                <el-input size="small" class="tagInput" v-if="scope.row.tagInputVisible" ref="saveTagInput" @blur="loseBlur(scope.row)" @keyup.enter.native="loseBlur(scope.row)" v-model="scope.row.tagInputValue"></el-input>
                <!--  -->
                <el-button size="small" v-else @click="showTagInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!--  -->
    <el-dialog :title="activeName === 'many' ? '' : ''" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <!--  -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
        <el-form-item :label="activeName === 'many' ? '' : ''" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addNewParams"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import mix from './Params-mixins.js'
export default {
  mixins: [mix]
}
</script>

<style lang="less" scoped>
.cascader {
  margin: 15px 0;
}
.el-tag {
  margin: 5px;
}
.tagInput {
  width: 120px;
}
</style>



