export default {
  data() {
    return {
      // 
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 
      cateList: [],
      // 
      total: 0,
      // 
      columns: [
        {
          label: '',
          prop: 'cat_name'
        },
        {
          label: '',
          prop: 'cat_deleted',
          type: 'template',
          template: 'isok'
        },
        {
          label: '',
          prop: 'cat_level',
          type: 'template',
          template: 'order'
        },
        {
          label: '',
          //  type: 'template' 
          type: 'template',
          //  opt
          template: 'opt',
          width: '200px'
        }
      ],
      // 
      addDialogVisible: false,
      // 
      addForm: {
        // 
        cat_name: '',
        // Id 0
        cat_pid: 0,
        // 0 
        cat_level: 0
      },
      // 
      addFormRules: {
        cat_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      parentCateList: [],
      // Id
      selectedCateList: [],
      // 
      cascaderConfig: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      }
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get('categories', { params: this.queryInfo })
      if (res.meta.status !== 200) return this.$message.error('')
      this.cateList = res.data.result
      this.total = res.data.total
      console.log(res)
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCateList()
    },
    // 
    async showAddDialog() {
      // 
      const { data: res } = await this.$http.get('categories', { params: { type: 2 } })
      if (res.meta.status !== 200) return this.$message.error('')
      // data
      this.parentCateList = res.data
      console.log(res.data)
      this.addDialogVisible = true
    },
    //  
    handleChange() {
      console.log(this.selectedCateList)
      if (this.selectedCateList.length === 0) {
        // 
        this.addForm.cat_pid = 0
        this.addForm.cat_level = 0
      } else {
        // 
        // Id
        this.addForm.cat_pid = this.selectedCateList[this.selectedCateList.length - 1]
        // 
        this.addForm.cat_level = this.selectedCateList.length
      }
    },
    // 
    addNewCate() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return

        const { data: res } = await this.$http.post('categories', this.addForm)
        if (res.meta.status !== 201) return this.$message.error('')
        this.$message.success('')
        this.getCateList()
        this.addDialogVisible = false
      })
    },
    // 
    resetForm() {
      this.$refs.addFormRef.resetFields()
      this.selectedCateList = []
      // 
      this.addForm.cat_pid = 0
      this.addForm.cat_level = 0
    }
  }
}



