<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-button type="primary" @click="showAddDialog"></el-button>

      <!--  -->
      <!-- https://github.com/MisterTaki/vue-table-with-tree-grid  -->
      <tree-table :data="cateList" :columns="columns" border :selection-type="false" :expand-type="false" show-index index-text="#" class="tree-table">
        <!--  -->
        <template slot="opt" slot-scope="scope">
          <el-button type="primary" size="mini" icon="el-icon-edit"></el-button>
          <el-button type="danger" size="mini" icon="el-icon-delete"></el-button>
        </template>
        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag type="success" v-else-if="scope.row.cat_level === 1"></el-tag>
          <el-tag type="warning" v-else></el-tag>
        </template>
        <!--  -->
        <template slot="isok" slot-scope="scope">
          <i class="el-icon-success" style="color:#20B2AA;" v-if="scope.row.cat_deleted === false"></i>
          <i class="el-icon-error" style="color:#F92672;" v-else></i>
        </template>
      </tree-table>

      <!--  -->
      <el-pagination @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-size="queryInfo.pagesize" layout="total, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="resetForm">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <!-- options  -->
          <!-- props  -->
          <!-- v-model  -->
          <!-- change  change -->
          <el-cascader expand-trigger="hover" :options="parentCateList" :props="cascaderConfig" v-model="selectedCateList" @change="handleChange" change-on-select clearable>
          </el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addNewCate"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import mix from './Cate-mixins.js'
export default {
  mixins: [mix]
}
</script>

<style lang="less" scoped>
.tree-table {
  margin-top: 15px;
}
.el-cascader{
  width: 100%;
}
</style>



