<template>
  <div class="app-container">
    <div class="filter-container">
      <el-table :data="tableData" ref="tableData" style="width: 100%" border stripe>
        <el-table-column show-overflow-tooltip align="center" type="index" width="50" label=""></el-table-column>
        <el-table-column show-overflow-tooltip sortable min-width="100" align="center" prop="execute_id" label=""></el-table-column>
        <el-table-column show-overflow-tooltip sortable min-width="100" align="center" prop="tasks_id" label="id"></el-table-column>
        <el-table-column show-overflow-tooltip sortable min-width="200" align="center" prop="url" label="">
        </el-table-column>
        <el-table-column show-overflow-tooltip sortable min-width="80" align="center" prop="price" label=""></el-table-column>
        <el-table-column show-overflow-tooltip sortable min-width="100" align="center" prop="tasks_name" label=""></el-table-column>
        <el-table-column show-overflow-tooltip sortable min-width="120" prop="complete_user" align="center" label=""></el-table-column>
        <el-table-column show-overflow-tooltip sortable min-width="120" prop="state" align="center" label=""></el-table-column>
        <el-table-column show-overflow-tooltip sortable min-width="200" prop="image" align="center" label="">
          <template  slot-scope="scope">
            <el-image
              style="width: 100px; height: 100px"
              :src="scope.row.image[0]"
              :preview-src-list="scope.row.image">
            </el-image>
          </template>
        </el-table-column>
        <el-table-column prop="control" align="center" label="" fixed="right" min-width='180px'>
          <template slot-scope="scope">
            <el-button type="primary" @click='handleDetails(scope.$index, scope.row)'></el-button>
            <el-button type="primary" @click="handleEdit(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination background layout="total, sizes, prev, pager, next, jumper" :current-page="currentPage"
                     :page-size="pageSize" :total="total" @size-change="handleSizeChange" @current-change="handleCurrentChange"></el-pagination>
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="form" ref="form">
          <el-form-item v-show="!isPass" prop="reson" label="" :label-width="formLabelWidth">
            <el-input v-model="form.reson" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item v-show="isPass" prop="member" label="" :label-width="formLabelWidth">
<!--            <el-select v-model="form.member" placeholder="">-->
<!--              <el-option label="" value=""></el-option>-->
<!--              <el-option label="" value=""></el-option>-->
<!--            </el-select>-->
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="submit"> </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import {
  Tree,
  Dialog,
  Table,
  TableColumn,
  Pagination,
  MessageBox,
  Message,
  Image,
  Drawer
} from 'element-ui'
export default {
  name: 'member',
  components: {
    'el-table': Table, // 
    'el-table-column': TableColumn, // 
    'el-pagination': Pagination, // 
    'el-dialog': Dialog, // 
    'el-drawer': Drawer, // 
    'el-image': Image // 

  },
  data() {
    return {
      total: 0, // 
      pageSize: 20, // 
      currentPage: 1, // 
      tableData: [], // 
      dialogFormVisible: false,
      form: {
        reson: '',
        member: ''
      },
      formLabelWidth: '120px',
      isPass: false,
      rowData: ''
    }
  },
  created() {
    this.getList()
  },
  methods: {
    // 
    async getList() {
      let data = {
        page: this.page,
        page_size: this.page_size
      }
      const { data: res } = await this.$http.get('/api/check_complete_tasks/', data)
      console.log(res)
      this.tableData = res.results
      this.total = res.count
    },
    // 
    handleDetails(index, row) {
      this.dialogFormVisible = true
      this.isPass = true
      this.rowData = row
    },
    // 
    handleEdit(index, row) {
      this.dialogFormVisible = true
      this.isPass = false
      this.rowData = row
    },
    submit() {
      if (this.isPass) { // 
        console.log(this.form)
        console.log(this.rowData)
        let data = {
          // tasks_id: this.rowData.tasks_id,
          // type: this.form.member,
          state: '2'
        }
        const { data: res } = this.$http.patch(`/api/complete_tasks/${this.rowData.execute_id }/`, data)
      } else { // 
        console.log(this.form)
        console.log(this.rowData)
        let data = {
          // tasks_id: this.rowData.tasks_id,
          remarks: this.form.reson,
          state: '1'
        }
        const { data: res } = this.$http.patch(`/api/complete_tasks/${this.rowData.execute_id}/`, data)
      }
      // this.$refs.form.resetFields() // 
      this.dialogFormVisible = false // 
      setTimeout(this.getList(), 2000)
    },
    // 
    handleSizeChange(val) {
      this.pageSize = val
      this.getLists()
    },
    // 
    handleCurrentChange(val) {
      this.currentPage = val
      this.getLists()
    }
  }
}
</script>
<style>

</style>



